import React, { Fragment, useEffect, useState } from 'react'
import axios from 'axios'
import jwtDefaultConfig from '../../../@core/auth/jwt/jwtDefaultConfig'
import {
    ChevronDown, Share, Printer, FileText, File, Grid, Copy, Plus, MoreVertical, Edit, Archive, Trash, Search, Eye, Lock, ChevronLeft
} from 'react-feather'
import {
    Card, CardHeader, CardTitle, Button, UncontrolledButtonDropdown, DropdownToggle, DropdownMenu, DropdownItem, Input, Label, Row, Col, Badge, Form, FormGroup, UncontrolledDropdown, Spinner, CardBody, TabContent, TabPane, Nav, NavItem, NavLink, InputGroup, InputGroupAddon, InputGroupText, CustomInput
} from 'reactstrap'
import useJwt from '@src/auth/jwt/useJwt'
import { Error, Success, ErrorMessage } from '../../viewhelper'
import { Link, useHistory } from 'react-router-dom'
import Select from 'react-select'
import { selectThemeColors, transformInToFormObject } from '@utils'
import { divIcon } from 'leaflet'

const CreatePointRule = () => {
    const BusinessList = JSON.parse(localStorage.getItem('customerBusinesses'))
    const [pointRuleloading, setPointRuleloading] = useState(false)
    const [userInput, setUserInput] = useState({
        skuamount: 0,
        skupoints: 0,
        skustartrange: 0,
        skuendrange: 0,
        product_id: 0,
        isrange: false
    })

    const handleChange = (e) => {
        setUserInput({ ...userInput, [e.target.name]: e.target.value })
    }
  
    const onSubmit = (e) => {
        e.preventDefault()
        localStorage.setItem('usePMStoken', true)
        const { skuamount, skupoints, skustartrange, skuendrange, product_id, isrange } = userInput
        setPointRuleloading(true)
        const merchantId = BusinessList[0].pms_merchantid
        useJwt.setMyRules(merchantId, { skuamount, skupoints, skustartrange, skuendrange, product_id, isrange }).then((response) => {
            setPointRuleloading(false)
            localStorage.setItem('usePMStoken', false)
            Success(response)
          }).catch((error) => {
            setPointRuleloading(false)
            localStorage.setItem('usePMStoken', false)
            Error(error)
            console.log(error)
          })
    }
    return (
        <Fragment>
            <Button.Ripple className='mb-1' color='primary' tag={Link} to='/PointRuleList' >
                    <div className='d-flex align-items-center'>
                            <ChevronLeft size={17} style={{marginRight:'5px'}}/>
                            <span >Back</span>
                    </div>
                    </Button.Ripple>
            <Card>
                <CardHeader className='border-bottom'>
                    <CardTitle tag='h4'>Set Rule</CardTitle>
                    
                </CardHeader>
                <CardBody style={{ paddingTop: '15px' }}>
                    <Form className="row" style={{ width: '100%' }} onSubmit={onSubmit} autoComplete="off">
                        <Col sm="6" >
                            <FormGroup>
                                <Label for="skupoints">SKU Points</Label>
                                <Input type="number"
                                    name="skupoints"
                                    id='skupoints'
                                    value={userInput.skupoints}
                                    onChange={handleChange}
                                    required
                                    placeholder="0"
                                            />
                            </FormGroup>
                          </Col>
                        <Col sm="12" className='mb-1' >
                            <FormGroup check>
                                <Input onChange={(e) => {
                                        if (e.target.checked) { 
                                            setUserInput({ ...userInput, isrange: true })
                                        } else {
                                            setUserInput({ ...userInput, isrange: false })
                                        }
                                    }
                                } type='checkbox' id='viaEmail' />
                                <Label for='viaEmail' check>
                                    Is Range
                                </Label>
                            </FormGroup>
                        </Col>
                        {userInput.isrange ?  <Col sm="6" >
                                    <FormGroup>
                                        <Label for="skustartrange">SKU Start Range</Label>
                                        <Input type="number"
                                            name="skustartrange"
                                            id='skustartrange'
                                            value={userInput.skustartrange}
                                            onChange={handleChange}
                                            required
                                            placeholder="0"
                                        />
                                    </FormGroup>
                                </Col> : null}
                        { userInput.isrange ? <Col sm="6" >
                                    <FormGroup>
                                        <Label for="skuendrange">SKU End Range</Label>
                                        <Input type="number"
                                            name="skuendrange"
                                            id='skuendrange'
                                            value={userInput.skuendrange}
                                            onChange={handleChange}
                                            required
                                            placeholder="0"
                                        />
                                    </FormGroup>
                                </Col> : null}
                        {userInput.isrange ? null :   <Col sm="6" >
                            <FormGroup>
                                <Label for="skuamount">SKU Amount</Label>
                                <Input type="number"
                                    name="skuamount"
                                    id='skuamount'
                                    value={userInput.skuamount}
                                    onChange={handleChange}
                                    required
                                    placeholder="0"
                                />
                            </FormGroup>
                          </Col>
                        }
                        {userInput.isrange ? null :  <Col sm="6" ></Col>}
                    
                        <Col sm="6" >
                            <FormGroup>
                                <Label for="product_id">Product ID</Label>
                                <Input type="number"
                                    name="product_id"
                                    id='product_id'
                                    value={userInput.product_id}
                                    onChange={handleChange}
                                    required
                                    placeholder="521336"
                                />
                            </FormGroup>
                        </Col>

                        <Col sm="12" className='text-center'>
                            {
                                pointRuleloading ? <Button.Ripple color='primary' className='mr-1' disabled style={{ marginTop: '25px' }}>
                                    <Spinner color='white' size='sm' />
                                    <span className='ml-50'>Loading...</span>
                                </Button.Ripple> : <Button.Ripple className='ml-2' color='primary' type="submit" style={{ marginTop: '25px' }}>
                                    <span >Submit</span>
                                </Button.Ripple>
                            }
                        </Col>
                    </Form>
                </CardBody>
            </Card>
        </Fragment>
    )
}

export default CreatePointRule